@media (width < 30em) {
  .titleContainer > div {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
  }
}

@media (width < 800px) {
  .titleContainer > div > * {
    margin-inline: 1rem;
  }
  .listItemContainer {
    padding-inline: 1rem;
  }
}
